import Head from "next/head";
import React, { useState, useEffect } from "react";

const Page = () => {
  const UNSPLASH_API_KEY =
    "66a4f8ef5dece777eadefe5ebaadeaa6602162c95da2f396e42ff77e1a074920";
  const UNSPLASH_URL = `https://api.unsplash.com/photos/random/?client=${UNSPLASH_API_KEY}&query=landscape&orientation=landscape`;
  const [background, setBackground] = useState();

  const loadBackground = () => {
    const savedImage = localStorage.getItem("bg");

    if (savedImage === null) {
      getBackground();
    } else {
      const parsedImage = JSON.parse(savedImage);
      const today = new Date();

      if (today > parsedImage.expiresOn) {
        getBackground();
      } else {
        setBackground(parsedImage.url);
      }
    }
  };
  const saveBackground = (imageUrl, city, country, name) => {
    const savedImage = localStorage.getItem("bg");

    if (savedImage !== null) {
      localStorage.removeItem("bg");
    }

    const expDate = new Date();
    expDate.setDate(expDate.getDate() + 1);

    const imgObject = {
      url: imageUrl,
      expiresOn: expDate,
      city,
      country,
      name
    };

    localStorage.setItem("bg", JSON.stringify(imgObject));
    loadBackground();
    return;
  };
  const getBackground = () => {
    fetch(UNSPLASH_URL)
      .then(response => response.json())
      .then(json => {
        const image = json;

        if (image.urls && image.urls.full && image.location) {
          const fullUrl = image.urls.full;
          const location = image.location;
          const city = location.city;
          const country = location.country;
          const name = location.name;

          saveBackground(fullUrl, city, country, name);
        } else {
          getBackground();
        }
      });

    return;
  };

  useEffect(() => {
    loadBackground();
  }, []);

  return (
    <div>
      <Head>
        <title>Momentum Examples</title>
        <link href="/static/css/momentum/style.css" rel="stylesheet" />
      </Head>
      <section className="center">
        <div className="js-clock clock">
          <h3 className="clock__text">00:00</h3>
        </div>
        <div className="js-name name" />
        <div>
          <form className="js-to-do to-do">
            <input
              className="js-add-to-do to-do__add-to-do"
              placeholder="Write a To Do here"
              type="text"
            />
          </form>
          <ul className="js-list list" />
        </div>
      </section>
      <div className="js-location location">
        <span className="location__text" />
      </div>
      <div className="js-weather weather">
        <span className="weather__text" />
      </div>
    </div>
  );
};

export default Page;
